import React, { Component } from "react";
import {
  Card,
  Table,
  Modal,
  message,
  Icon,
  Input,
  Select,
  ConfigProvider,
  DatePicker,
  Button
} from "antd";
import "moment/locale/zh-cn";
// import ReactHTMLTableToExcel from "react-html-table-to-excel";
import LinkButton from "../../../components/link-button/index";
import { formateDate } from "../../../utils/dateUtils";
import zh_CN from "antd/lib/locale-provider/zh_CN";
import "moment/locale/zh-cn";
import { reqOrder_list, downloadList } from "../../../api/index";

const { RangePicker } = DatePicker;
class Order_list extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.state = {
      data: [],
      count: 0,
      pageSize: 20,
      start_time: "",
      end_time: "",
      order_status: "",
      type: "",
      paramKey: "",
      inputParam: "",
      user_id: "",
      order_id: ""
    };
    this.initColumns();
  }
  getUsers = async (page, limit) => {
    const result = await reqOrder_list(page, limit);
    if (result.status === 0) {
      this.setState({
        data: result.data,
        count: parseInt(result.count)
      });
    } else {
      message.error("");
    }
  };
  handleChange(event) {
    this.setState({ inputParam: event.target.value });
  }
  onSearchData = async () => {
    const result = await reqOrder_list(1, 20, this.state);
    if (result.status === 0) {
      this.setState({
        data: result.data,
        count: parseInt(result.count)
      });
    }
  };
  download = () => {
    downloadList(this.state);
  };
  componentDidMount() {
    this.getUsers(1, 20);
  }
  render() {
    return (
      <Card
        title={
          <div>
            <div>
              <ConfigProvider locale={zh_CN}>
                <RangePicker
                  // defaultValue={[moment().locale("zh-cn")]}
                  // showTime={{ format: "HH:mm" }}
                  format="YYYY-MM-DD"
                  placeholder={["", ""]}
                  onChange={this.dataPickerOnChange}
                />
              </ConfigProvider>
              &nbsp; &nbsp;
              <Select
                placeholder=""
                style={{ width: 120 }}
                onSelect={value => this.setState({ paramKey: value })}
              >
                <Select.Option value="order_id">id</Select.Option>
                <Select.Option value="user_id">user_id</Select.Option>
                <Select.Option value="1"></Select.Option>
                <Select.Option value="2"></Select.Option>
              </Select>
              &nbsp; &nbsp;
              <Input
                type="text"
                placeholder=""
                style={{ width: 150 }}
                value={this.state.inputParam}
                onChange={this.handleChange}
              />
              &nbsp; &nbsp;
              <Select
                defaultValue=""
                style={{ width: 200 }}
                onSelect={value => this.setState({ order_status: value })}
              >
                <Select.Option value=""></Select.Option>
                <Select.Option value="0"></Select.Option>
                <Select.Option value="1"></Select.Option>
                <Select.Option value="4"></Select.Option>
                <Select.Option value="5"></Select.Option>
                <Select.Option value="6"></Select.Option>
                <Select.Option value="7"></Select.Option>
                <Select.Option value="8"></Select.Option>
                <Select.Option value="9"></Select.Option>
                <Select.Option value="10"></Select.Option>
                <Select.Option value="11"></Select.Option>
              </Select>
              &nbsp; &nbsp;
              <Select
                defaultValue=""
                style={{ width: 120 }}
                onSelect={value => this.setState({ type: value })}
              >
                <Select.Option value=""></Select.Option>
                <Select.Option value="0"></Select.Option>
                <Select.Option value="1">alipay</Select.Option>
                <Select.Option value="2"></Select.Option>
                <Select.Option value="3"></Select.Option>
                <Select.Option value="4"></Select.Option>
                <Select.Option value="6"></Select.Option>
                <Select.Option value="7"></Select.Option>
                <Select.Option value="8"></Select.Option>
                <Select.Option value="9"></Select.Option>
              </Select>
              &nbsp; &nbsp;
              <button onClick={this.onSearchData}>
                <Icon type="search" />
                
              </button>
              &nbsp; &nbsp;
            </div>
            <div style={{ marginTop: 10 }}>
              <Input
                type="text"
                placeholder="user_id"
                style={{ width: 150 }}
                value={this.state.user_id}
                onChange={e => this.setState({ user_id: e.target.value })}
              />
              &nbsp; &nbsp;
              <Input
                type="text"
                placeholder="Id"
                style={{ width: 300 }}
                value={this.state.order_id}
                onChange={e => this.setState({ order_id: e.target.value })}
              />
              &nbsp; &nbsp;
              <button onClick={this.onSearchData}>
                <Icon type="search" />
                
              </button>
            </div>
          </div>
        }
        extra={
          <span>
            <Button style={{ float: "right" }} onClick={() => this.getUsers(1, 20)} icon="reload" />
            <br />
            <br />
            <Button onClick={this.download} icon="download">
              
            </Button>
          </span>
        }
      >
        <Table
          bordered
          size="small"
          rowKey="id"
          dataSource={this.state.data}
          columns={this.initColumns()}
          pagination={{
            defaultPageSize: this.state.pageSize,
            showSizeChanger: true,
            showQuickJumper: true,
            defaultCurrent: 1,
            total: this.state.count,
            onChange: (page, pageSize) => {
              if (page && pageSize) {
                this.setState({
                  pageSize: pageSize
                });
                this.getUsers(page, pageSize);
              } else return;
            },
            onShowSizeChange: (current, size) => {
              if (size) {
                this.getUsers(current, size);
              } else return;
            }
          }}
          scroll={{ x: 1900, y: 600 }}
        />
      </Card>
    );
  }
  initColumns = () => [
    {
      title: "ID",
      dataIndex: "order_id",
      width: 300
    },
    {
      title: "user_id",
      dataIndex: "user_id",
      width: 100
    },
    {
      title: "",
      dataIndex: "user_name",
      width: 100
    },
    {
      title: "",
      dataIndex: "package_nick",
      width: 100
    },
    {
      title: "",
      dataIndex: "proxy_user_id",
      width: 100
    },
    {
      title: "",
      dataIndex: "channel_type",
      width: 100,
      render: (text, record, index) => {
        let word;
        switch (text) {
          case "0":
            word = record.replace_id;
            break;
          case "5":
            word = "UC";
            break;
          case "12":
            word = "onePay";
            break;
          case "11":
            word = "";
            break;
          case ["10", "13", "14", "15", "16"]:
            word = "";
            break;
          default:
            word = "";
            break;
        }
        return <span>{word}</span>;
      }
    },
    {
      title: "",
      dataIndex: "type",
      width: 100,
      render: (text, record, index) => {
        let word;
        switch (text) {
          case "1":
            word = "alipay";
            break;
          case "2":
            word = "";
            break;
          case "3":
            word = "";
            break;
          case "4":
            word = "";
            break;
          case "5":
            word = "";
            break;
          case "6":
            word = "";
            break;
          case "7":
            word = "";
            break;
          case "8":
            word = "";
            break;
          case "9":
            word = "";
            break;
          default:
            word = "";
            break;
        }
        return <span>{word}</span>;
      }
    },
    {
      title: "",
      dataIndex: "amount",
      width: 100,
      sorter: (a, b) => a.amount - b.amount
    },
    {
      title: "",
      dataIndex: "arrival_amount",
      width: 100,
      sorter: (a, b) => a.arrival_amount - b.arrival_amount
    },
    {
      title: "",
      dataIndex: "status",
      width: 130,
      render: (text, record, index) => {
        let word;
        switch (text) {
          case "1":
            word = "";
            break;
          case "2":
            word = "";
            break;
          case "3":
            word = "";
            break;
          case "4":
            word = "";
            break;
          case "5":
            word = "";
            break;
          case "6":
            word = "";
            break;
          case "7":
            word = "";
            break;
          case "8":
            word = "";
            break;
          case "9":
            word = "";
            break;
          case "10":
            word = "";
            break;
          case "11":
            word = "";
            break;
          case "12":
            word = "";
            break;
          default:
            word = "";
            break;
        }
        return <span>{word}</span>;
      }
    },
    {
      title: "",
      dataIndex: "created_at",
      width: 200,
      render: formateDate,
      sorter: (a, b) => a.created_at - b.created_at
    },
    {
      title: "",
      dataIndex: "arrival_at",
      width: 200,
      render: (text, record, index) => {
        if (text === "0" || !text) {
          return "";
        } else return formateDate(text);
      },
      sorter: (a, b) => a.arrival_at - b.arrival_at
    },
    {
      title: "",
      dataIndex: "handle",
      width: 100
    },
    {
      title: "",
      dataIndex: "remark"
    }
  ];
  dataPickerOnChange = (date, dateString) => {
    let startTime = dateString[0] + " 00:00:00";
    let endTime = dateString[1] + " 00:00:00";
    this.setState({
      start_time: startTime,
      end_time: endTime
    });
  };
}

export default Order_list;
