import React, { Component } from "react";
import {
  Card,
  Table,
  Modal,
  message,
  DatePicker,
  Icon,
  Select,
  Input,
  ConfigProvider
} from "antd";
import zh_CN from "antd/lib/locale-provider/zh_CN";
// import moment from "moment";
import "moment/locale/zh-cn";
import { formateDate } from "../../utils/dateUtils";
import LinkButton from "../../components/link-button/index";
import {
  reqUsers,
  setGameUserNickName,
  changeGold,
  searchData,
  reqLoadGold
} from "../../api/index";
import WrappedNormalLoginForm from "././user-nick";

const { RangePicker } = DatePicker;
const { Option } = Select;
export default class User extends Component {
  constructor(props) {
    super(props);
    this.handleChange = this.handleChange.bind(this);
    this.initColumns();
  }
  state = {
    data: [],
    count: 0,
    pageSize: 20,
    isNickShow: false,
    isGoldShow: false,
    game_nick: "",
    startTime: "",
    endTime: "",
    inputParam: {
      key: "id",
      val: ""
    }
  };

  initColumns = () => [
    {
      title: "user_id",
      dataIndex: "id",
      key: "id",
      fixed: "left",
      width: 150
    },
    {
      title: "",
      dataIndex: "game_nick",
      key: "game_nick",
      fixed: "left",
      width: 150,
      onCell: (record, rowIndex) => {
        return {
          onClick: event => {
            this.game_nick = record.game_nick;
            this.id = record.id;
            this.rowIndex = rowIndex;
            this.setState({
              isNickShow: true
            });
          }, // 
          onDoubleClick: event => {},
          onContextMenu: event => {},
          onMouseEnter: event => {
            event.target.style.cursor = "pointer";
          }, // 
          onMouseLeave: event => {}
        };
      }
    },

    {
      title: "",
      dataIndex: "game_gold",
      key: "game_gold",
      sorter: (a, b) => a.game_gold - b.game_gold,
      width: 150,
      onCell: (record, rowIndex) => {
        return {
          onClick: event => {
            this.goldRecord = record;
            this.setState({
              isGoldShow: true
            });
          }, // 
          onDoubleClick: event => {},
          onContextMenu: event => {},
          onMouseEnter: event => {
            event.target.style.cursor = "pointer";
          }, // 
          onMouseLeave: event => {}
        };
      }
    },
    {
      title: "",
      dataIndex: "package_nick",
      key: "package_nick",
      width: 100
    },
    {
      title: "",
      dataIndex: "proxy_nick",
      key: "proxy_nick",
      width: 150
    },
    {
      title: "",
      dataIndex: "phone_number",
      key: "phone_number",
      width: 150
    },
    {
      title: "",
      dataIndex: "role_name",
      key: "role_name",
      width: 150
    },
    {
      title: "",
      dataIndex: "regin_time",
      render: formateDate,
      key: "regin_time",
      sorter: (a, b) => a.regin_time - b.regin_time,
      width: 200
    },
    {
      title: "IP",
      dataIndex: "login_ip",
      key: "login_ip",
      width: 200
    },
    {
      title: "",
      dataIndex: "login_time",
      render: formateDate,
      sorter: (a, b) => a.login_time - b.login_time,
      width: 200
    },
    {
      title: "",
      dataIndex: "handle",
      render: record => (
        <span>
          <LinkButton onClick={() => this.getGoldDetail(record)}>
            
          </LinkButton>
          <LinkButton onClick={() => this.moreDetail(record)}></LinkButton>
        </span>
      )
    },
    {
      title: "",
      width: 100,
      render: record => (
        <span>
          <LinkButton onClick={() => this.getLoadGold(record)}></LinkButton>
        </span>
      )
    }
  ];

  getUsers = async (page, limit) => {
    const result = await reqUsers(page, limit);
    if (result.status === 0) {
      const { game_user, proxy_user } = result.data;
      game_user.forEach(element => {
        proxy_user.forEach(item => {
          if (element.id === item.id) {
            element.proxy_nick = item.proxy_pid;
          }
        });
      });
      this.setState({
        data: game_user,
        count: result.count
      });
    } else {
      message.error(result.msg);
    }
  };
  changeNickName = () => {
    let form = this.refs.getFormValue; //refsform
    form.validateFields(async (err, value) => {
      if (!err) {
        this.setState({ isNickShow: false });
        if (value.name !== this.game_nick) {
          const result = await setGameUserNickName(this.id, value.name);
          if (result.status === 0) {
            message.success("!");
            // , 
            let index = this.rowIndex;
            // 
            this.state.data[index][`game_nick`] = value.name;
            // ,  setState arr
            this.setState({
              data: this.state.data
            });
          }
        }
      }
      form.resetFields();
    });
  };
  changeGold = () => {
    //user-nickinputvalue.name
    let form = this.refs.getFormValue;
    let goldRecord = this.goldRecord;
    form.validateFields(async (err, value) => {
      const res = await changeGold(goldRecord, value);
      if (res.status === 0) {
        message.success(res.msg);
        this.setState({ isGoldShow: false });
      } else {
        message.error(res.msg);
      }
      form.resetFields();
    });
  };
  dataPickerOnChange = (date, dateString) => {
    let startTime = dateString[0] + " 00:00:00";
    let endTime = dateString[1] + " 00:00:00";
    this.setState({
      startTime: startTime,
      endTime: endTime
    });
  };
  handleChange(event) {
    let data = Object.assign({}, this.state.inputParam, {
      val: event.target.value
    });
    this.setState({ inputParam: data });
  }
  onSearchData = async () => {
    const result = await searchData(
      1,
      this.state.pageSize,
      this.state.startTime,
      this.state.endTime,
      this.state.inputParam
    );
    if (result.status === 0) {
      const { game_user, proxy_user } = result.data;
      game_user.forEach(element => {
        proxy_user.forEach(item => {
          if (element.id === item.id) {
            element.proxy_nick = item.proxy_pid;
          }
        });
      });
      this.setState({
        data: game_user,
        count: result.count
      });
    } else {
      message.error("");
    }
  };
  getLoadGold = async record => {
    const id = record.id;
    const result = await reqLoadGold(id);
    if (result.status === 0) {
      Modal.success({
        title: "",
        content: `${record.id} : ${result.data[0].game_gold}`
      });
    }
  };
  getGoldDetail = async record => {
    // const id = record.id;
    // const result = await reqLoadGold(id);
    // Modal.success({
    //   title: "",
    //   content: `${record.id} : ${result.data[0].game_gold}`
    // });
  };
  componentDidMount() {
    this.getUsers(1, 20);
  }
  render() {
    const { data, count } = this.state;
    const title = (
      <span>
        <ConfigProvider locale={zh_CN}>
          <RangePicker
            // defaultValue={[moment().locale("zh-cn")]}
            // showTime={{ format: "HH:mm" }}
            format="YYYY-MM-DD"
            placeholder={["", ""]}
            onChange={this.dataPickerOnChange}
          />
        </ConfigProvider>
        &nbsp; &nbsp;
        <Select
          style={{ width: 200 }}
          placeholder="Select a person"
          defaultValue="id"
          onChange={val => {
            let data = Object.assign({}, this.state.inputParam, { key: val });
            this.setState(
              {
                inputParam: data
              },
              () => {
                console.log(this.state);
              }
            );
          }}
        >
          <Option value="id">user_id</Option>
          <Option value="game_nick"></Option>
          <Option value="phone_number"></Option>
          <Option value="role_name"></Option>
          <Option value="proxy_nick"></Option>
          <Option value="package_nick"></Option>
        </Select>
        &nbsp; &nbsp;
        <Input
          type="text"
          placeholder=""
          style={{ width: 150 }}
          value={this.state.inputParam.val}
          onChange={this.handleChange}
        />
        &nbsp; &nbsp;
        <button onClick={this.onSearchData}>
          <Icon type="search" />
        </button>
      </span>
    );
    const extra = (
      <button onClick={() => this.getUsers(1, 20)}>
        <Icon type="reload" />
      </button>
    );
    return (
      <Card title={title} extra={extra}>
        <Table
          bordered
          rowKey="_id"
          dataSource={data}
          columns={this.initColumns()}
          pagination={{
            defaultPageSize: this.state.pageSize,
            showSizeChanger: true,
            showQuickJumper: true,
            defaultCurrent: 1,
            total: count,
            onChange: (page, pageSize) => {
              this.getUsers(page, pageSize);
              this.setState({
                pageSize: pageSize
              });
            },
            onShowSizeChange: (current, size) => {
              this.getUsers(current, size);
            }
          }}
          scroll={{ x: 2000, y: 600 }}
        />
        <Modal
          title=""
          visible={this.state.isNickShow}
          onOk={this.changeNickName}
          onCancel={() => {
            this.setState({ isNickShow: false });
          }}
        >
          <WrappedNormalLoginForm
            ref="getFormValue"
            val={this.game_nick}
            isNickModal
          />
        </Modal>
        <Modal
          title=""
          visible={this.state.isGoldShow}
          onOk={this.changeGold}
          onCancel={() => {
            this.setState({ isGoldShow: false });
          }}
        >
          <WrappedNormalLoginForm
            ref="getFormValue"
            goldRecord={this.goldRecord}
          />
        </Modal>
      </Card>
    );
  }
}
