import React, { Component } from "react";
import { Form, Icon, Input } from "antd";

const FormItem = Form.Item;
class NormalLoginForm extends Component {
  render() {
    const { getFieldDecorator } = this.props.form;
    const { isNickModal, goldRecord } = this.props;
    return (
      <Form className="login-form">
        <FormItem style={isNickModal ? {} : { display: "none" }}>
          {getFieldDecorator("name", {
            rules: [
              {
                required: isNickModal,
                message: "Please input your username!"
              }
            ],
            initialValue: this.props.val
          })(
            <Input
              prefix={<Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />}
              placeholder=""
            />
          )}
        </FormItem>
        <FormItem label="" style={isNickModal ? { display: "none" } : {}}>
          {getFieldDecorator("gold", {
            rules: [
              {
                required: !isNickModal,
                whitespace: true,
                pattern: /^\d+(\.\d+)?$/,
                message: ""
              }
            ],
            initialValue: isNickModal ? "" : goldRecord.game_gold
          })(<Input placeholder="" />)}
        </FormItem>
        <FormItem label="" style={isNickModal ? { display: "none" } : {}}>
          {getFieldDecorator("desc", {
            rules: [
              { max: 400, message: "400" },
              {
                required: !isNickModal
              }
            ]
          })(
            <Input.TextArea
              placeholder=""
              autosize={{ minRows: 2, maxRows: 6 }}
            />
          )}
        </FormItem>
      </Form>
    );
  }
}

const WrappedNormalLoginForm = Form.create()(NormalLoginForm);

export default WrappedNormalLoginForm;
